import styled from "styled-components";

export const ExperienceSection = styled.section`
  bacground: ${({ theme }) => theme.body};
  color: ${({ theme }) => theme.text};
  min-width: 100vw;
  margin: 100px auto;
`;

export const ExperienceContainer = styled.div`
  display: flex;
  align-items: center;
  position: relative;
  width: 100%;

  div {
    width: 60%;
    margin-right: 0;
    margin-left: auto;
    margin-bottom: 20px;

    h1 {
      font-size: 35px;
      // color: ${({ theme }) => theme.text}
      transition: 0.3s ease;
      display: inline-block;
      position: relative;

      &::before {
        position: absolute;
        content: "";
        width: 200px;
        height: 2px;
        background: ${({ theme }) => theme.effect};
        top: 50%;
        left: -250px;
      }

      &:hover {
        color: ${({ theme }) => theme.effect};
      }
    }
  }

  .line {
    position: relative;
    flex-shrink: 0;
    width: 50%;
    margin-right: 30px;
    margin-left: 300px;
    max-width: 200px;
  }
`;
